import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import Wrap from 'components/wrap';
import { Header2 } from 'components/typography';
import useStyles from './use-styles';
import { DragScroll } from 'helpers/dragScroll';

function OurStory() {
	useEffect(() => {
		DragScroll('timeline');
	}, []);

	const styles = useStyles();

	const { payload } = useSelector((state) => state.AboutInfo);

	return (
		<section className={styles.story}>
			<Wrap>
				<Header2 title='Story of TGA and Founders' />
				<div id='timeline' className={styles.timeline}>
					<ol>
						{payload &&
							payload.timeLine.map((item) => (
								<li>
									<span className={styles.circle}></span>
									<div className={styles.info}>
										<span className={styles.year}>{item.time}:</span>
										<p className={styles.text}>{item.info}</p>
									</div>
								</li>
							))}
					</ol>
				</div>
			</Wrap>
		</section>
	);
}

export default OurStory;
